import React, { useEffect } from "react"
import { navigate } from "gatsby"

const shows = [
  { picture: "Doraemon", text: "Doraemon", displayText: "Doraemon" },
  {
    picture: "chotaBheem",
    text: "chota bheem pogo",
    displayText: "Chota Bheem",
  },
  { picture: "Hagimaru", text: "Hagimaru", displayText: "Hagimaru" },
  { picture: "Kiteratsu", text: "Kiteratsu", displayText: "Kiteratsu" },
  { picture: "Kochikame", text: "Kochikame", displayText: "Kochikame" },
  { picture: "MightyRaju", text: "Mighty Raju", displayText: "Mighty Raju" },
  {
    picture: "NinjaBoy",
    text: "Ninja boy Rantaro",
    displayText: "Ninja boy Rantaro",
  },
  { picture: "Oswald", text: "Oswald", displayText: "Oswald" },
  { picture: "Shinchan", text: "Shinchan", displayText: "Shinchan" },
  {
    picture: "DetectiveKonan",
    text: "Detective Konan",
    displayText: "Detective Konan",
  },
]

const languages = ["Telugu", "Tamil", "English", "Hindi"]

const HomePage = () => {
  useEffect(() => {
    // Retrieve cookies for last viewed show and language
    const lastShow = document.cookie
      .split("; ")
      .find(row => row.startsWith("lastShow="))
    const lastLanguage = document.cookie
      .split("; ")
      .find(row => row.startsWith("selectedLanguage="))

    const show = lastShow
      ? lastShow.split("=")[1]
      : shows[0].text.toLowerCase().replace(/\s+/g, "-")
    const language = lastLanguage
      ? lastLanguage.split("=")[1]
      : languages[0].toLowerCase()

    // Redirect to the last viewed show and language, or the first in the list
    navigate(`/${show}/${language}`)
  }, [])

  return null // The component doesn't render anything visible
}

export default HomePage
